html {
	background: #eee;
	min-width: 310px;
	height: 100%;
	margin: 0;
	padding: 0;
	-moz-text-size-adjust: none;
	-ms-text-size-adjust: none;
	-webkit-text-size-adjust: none;
	text-size-adjust: none;
}

body {
	height: 100%;
	padding: 0;
	margin: 0 auto;
	max-width: 42em;
	color: #333;
	-webkit-hyphens: auto;
	-ms-hyphens: auto;
	hyphens: auto;
	font-family: Arial, Helvetica, sans-serif;
}

main img,
.canvas-container-container {
	display: block;
	margin: 1.5em auto;
	max-width: 100%;
	height: auto;
}

.canvas-container-container {
	width: 640px;
}

.canvas-container {
	position: relative;
	width: 100%;
	padding-top: calc(200% / 3);
	/* 3 : 2 */
	overflow: hidden;
}

.canvas-container>canvas {
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	width: 100%;
	height: 100%;
	touch-action: none;
	-webkit-user-select:none;
	user-select:none;
}

.content-wrapper {
	min-height: 100%;
	margin-bottom: -24px;
	background: #ddd;
}

a:link, a:visited {
	text-decoration: none;
	color: #111;
}

a:hover, a:active {
	text-decoration: none;
	color: #000;
}

.content-padding {
	padding: 1em;
}

.site-footer {
	display: table;
	text-align: center;
	font-size: 12px;
	width: 100%;
}

.site-footer>* {
	display: table-cell;
	vertical-align: middle;
}

.site-footer,
.push {
	height: 24px;
	clear: both;
}

.axis-label {
	position: absolute;
	z-index: 1;
	text-shadow:
		-1px 1px 2px #eee,
		1px 1px 2px #eee,
		1px -1px 2px #eee,
		-1px -1px 2px #eee;
}

.axis-label-X {
	color: #900;
}

.axis-label-Y {
	color: #090;
}

.axis-label-W {
	color: #809;
}

@media (prefers-color-scheme: dark) {
	html {
		background: #111;
	}

	.content-wrapper {
		background: #222;
	}

	body {
		color: #ccc;
	}

	a:link, a:visited {
		color: #eee;
	}

	a:hover, a:active{
		color: #fff;
	}

	.axis-label {
		text-shadow:
			-1px 1px 2px #111,
			1px 1px 2px #111,
			1px -1px 2px #111,
			-1px -1px 2px #111;
	}

	.axis-label-X {
		color: #f77;
	}

	.axis-label-Y {
		color: #7f7;
	}

	.axis-label-W {
		color: #d4f;
	}
}

@media only screen and (min-width:480px),
only print {
	.content-wrapper {
		text-align: justify;
	}

	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		text-align: left;
	}
}

figcaption {
	font-style: italic;
	font-size: 80%;
	margin-top: .5em;
}

h1 {
	margin: 0;
}

header>p {
	font-style: italic;
	font-size: 80%;
	margin-top: .5em;
}

body {
	counter-reset: h2;
}

h2:before {
	content: counter(h2) ".\0a0";
	counter-increment: h2;
	counter-reset: h3;
}

h3:before {
	content: counter(h2) "."counter(h3) ".\0a0";
	counter-increment: h3;
}

.transform-table, .node-table {
	display: inline-block;
}
